/* 顶部广告幻灯片布局 */
.top_ {
  margin-bottom: 10px;
  width: 100%;
}
/* 下载排行猜你喜欢整体布局 */
.bottom_ {
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  display: flex;
}

/* 幻灯片内的图片平铺 */
.el-carousel__item img {
  width: 100%;
}

/* 每一行里面得缩略图文字信息 */
.out {
  width: 100%;
  height: 25px;
}
/* 每一行里面得缩略图图片 */
.in-img {
  width: 20%;
  height: 100%;
  float: left;
  margin-right: 25px;
  cursor: pointer;
}
/* 模块名称，时间，收藏布局 */
.in-text {
  float: left;
  width: 60%;
}
/* 模块名称 */
.text {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: bold;
  width: 100%;
  cursor: pointer;
}
.text:hover {
  font-size: 15px;
  color: #0681ec;
}
/* 模块得时间和收藏 */
.one {
  overflow: hidden;
  width: 100%;
  height: 10%;
}
/* 模块时间 */
.two {
  float: left;
  color: gray;
}
/* 模块收藏 */
.three {
  float: right;
  width: 30%;
  cursor: pointer;
}

.details_ {
  color: gray;
  cursor: pointer;
}
.details_number {
  color: gray;
}
.details_:hover {
  font-size: 15px;
  color: #0681ec;
  font-weight: bold;
}

.tableFooter {
  padding: 0.75rem 1.25rem;
  background-color: #fff;
  border-top: 1px solid rgba(26, 54, 126, 0.125);
  display: block !important;
  text-align: center !important;
  float: left;
  width: 89%;
}

.scroll-y {
  height: 430px;
}

.card {
  -webkit-box-shadow: 0 0.46875rem 2.1875rem rgba(4, 9, 20, 0.03),
    0 0.9375rem 1.40625rem rgba(4, 9, 20, 0.03),
    0 0.25rem 0.53125rem rgba(4, 9, 20, 0.05),
    0 0.125rem 0.1875rem rgba(4, 9, 20, 0.03);
  box-shadow: 0 0.46875rem 2.1875rem rgba(4, 9, 20, 0.03),
    0 0.9375rem 1.40625rem rgba(4, 9, 20, 0.03),
    0 0.25rem 0.53125rem rgba(4, 9, 20, 0.05),
    0 0.125rem 0.1875rem rgba(4, 9, 20, 0.03);
  border-width: 0;
  transition: all 0.2s; /* transition: all .2s; */
}
.scroll-y:hover {
  overflow: auto;
}
.scroll-y:not(:hover) {
  overflow: hidden;
}

.scroll-y::-webkit-scrollbar-thumb,
.bottom_::-webkit-scrollbar-thumb {
  background-color: rgb(170, 170, 170);
  -moz-border-radius: 2em;
  border-radius: 2em;
}
