/* 最新发布，行业热点采用的样式 */
.pos_left {
  flex: 1;
  margin-right: 10px;
  border-radius: 10px;
  padding: 5px;
  padding-bottom: 5px;
  min-width: 300px;
  background-color: white;
}
/* 猜你喜欢 */
.pos_like {
  flex: 1;
  min-width: 300px;
  margin-right: 10px;
  border-radius: 10px;
  padding: 5px;
  background-color: white;
}
/* 猜你喜欢顶部图片 */
.pos_like > img {
  width: 66%;
  height: 50px;
  float: left;
  text-align: center;
  padding-left: 68px;
}
/* 最新发布，行业热点外边框 */
.outBorder {
  /* border: 1px solid gray; */
  background-color: white;
  margin-top: 25px;
}
/* 最新发布，行业热点头部样式 */
.tableHeader {
  display: flex;
  background: linear-gradient(to right, rgb(185, 216, 124), rgb(147, 204, 32));
  height: 50px;
  border-radius: 5px 5px 0px 0px;
}
/* 猜你喜欢的列表部分 */
.rm-like-tb {
  float: left;
  margin-top: 25px;
  width: 100%;
}
/* 渐变蓝色背景 */
.blue {
  background: linear-gradient(to right, rgb(110, 184, 226), rgb(29, 187, 226));
}
/* 渐变橘色背景 */
.orange {
  background: linear-gradient(to right, rgb(255, 177, 118), rgb(253, 148, 9));
}
/* 最新发布，行业热点头部文字样式 */
.tableHeaderText {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: bold;
  padding: 0px 150px 0px 38px;
  text-align: center;
  line-height: 50px;
  color: white;
  font-size: 16px;
}
/* 最新发布，行业热点头部内小图标 */
.smallImg {
  width: 46px;
  height: 50px;
}
.defCollectColor {
  color: rgb(218, 115, 75);
}
